/*    projectSlider  .      
    ,    .   projectSlider  
    slick-slider ( slick). */
//   slick (https://kenwheeler.github.io/slick/).
.slick-slider {
    /*    slick  100% ..   ( modal-work__photo)
        (   ) - ,   
     . */
    height: 100%;
    display: flex;
    flex-direction: column;
    .slick-list {
        height: 100%;
        .slick-track {
            height: 100%;
        }
    }
    /*   slick-slider    slick-dots (  
     ).
      */
    .slick-dots {
        display: flex;
        justify-content: center;
        width: 100%;
        padding: 0;
        margin: 0;
        list-style: none;
        position: absolute;
        bottom: 1.467rem;
        left: 0;
        z-index: 2;
        li {
            margin: 0 0.5rem;
            //    li    slick-active ( ),  ..
            &.slick-active {
                // ..   background-color.
                button {
                    background-color: #fff;
                }
            }
            //     .
            button {
                padding: 0;
                //
                //   ,    button.
                font-size: 0; //   .
                color: transparent; //   .
                //
                //     .
                width: 11px;
                height: 11px;
                border: 1px solid #fff;
                border-radius: 50%;
                background: transparent;
                cursor: pointer;
                //   ( )   outline.
                &:focus {
                    outline: none;
                }
            }
        }
    }
}
