@import "../_vars.scss";
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= BLOCK: work =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
.work {
    border-radius: 10px;
    overflow: hidden; //   ,        border-radius.
    box-shadow: 0 0 4px rgba(#000, 0.25);
    &__content {
        padding: 1rem 0.8rem;
        .work__category {
            font-size: 0.8rem;
            color: $violet-dark;
            text-transform: uppercase;
            margin-bottom: 0.5rem;
        }
        .work__title {
            display: flex;
            justify-content: space-between;
            font-size: 1.067rem;
            font-weight: 700;
            color: $violet-dark;
            text-transform: uppercase;
        }
    }
    transition: box-shadow 0.2s linear;
    //     ( ).
    &:hover {
        box-shadow: 0 0 25px rgba($violet, 0.65);
    }
}
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MODIFIERS: work (connection at the end of styles .css/.scss) =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//

//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MEDIA QUERIES: work =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
/*   max-width (      ),
   min-width (      ). */
//
@media ($mobile) {
    .work {
        max-width: 370px; //      370px,     370px (    ).
    }
}

@media ($mobile-sm) {
    .work {
        margin: 0 auto;
        max-width: 370px; //      370px,     370px (    ).
    }
}

