// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-
// -#- IMPORTS - general styles -#-
// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-
//
// Variables (      ,    ).
@import "_vars.scss";
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= GLOBAL STYLES =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
html {
    box-sizing: border-box; //  , padding  border       ,         .
    font-size: 15px; //  ()  .
}

body {
    font-family: 'Montserrat', sans-serif; //  () .
    font-size: 1rem; //  ()  .
    color: $violet-dark; //  ()  .
    margin: 0;
    line-height: 20px; //  ()  .
    font-weight: 400; //  ()   (400 - regular).

    /*    ,    html   body  no-scroll,
          ,    ,
    ..        . */
    &.no-scroll {
        overflow: hidden;
    }
}

*,
*::before,
*::after {
    box-sizing: inherit; //     ( html).
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

a {
    color: $violet-dark; //  ()    .
    text-decoration: none; //     .
    display: inline-block; //  ,     -.
}

ul,
li {
    list-style: none; //      .
    margin: 0; //  margin.
    padding: 0; //  padding.
}

hr { //   .
    display: block;
    width: 100%;
    height: 1px;
    border: 0;
    background-color: #e5e5e5;
    margin: 40px 0;
}

p {
    margin: 0 0 1rem;
}

//     button.
button {
    background: 0;
    border: 0;
    padding: 0;
    cursor: pointer;
    // ..     button  ,     .
    font-family: inherit;
}

//     input.
input {
    // ..     input  ,     .
    font-family: inherit;
}




