@import "../vars";
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= SECTION: header =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
.header {
    padding-top: 30px;
    background-color: $violet-bright;
    position: absolute; //  body.
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    //      .
    &.fixed {
        padding: 10px 0;
        background-color: $violet-bright;
        box-shadow: 0 5px 10px rgba($violet, 0.65); //       .
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        transform: translate3d(0, 0, 0); //  ,      .
    }
}

.header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MODIFIERS: header (connection at the end of styles .css/.scss) =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//

//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MEDIA QUERIES: header =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
/*   max-width (      ),
   min-width (      ). */
//
@media ($mobile) {
    .header {
        background-color: $violet-bright;
        box-shadow: 0 5px 10px rgba($violet, 0.65);
        //  header,        .
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        padding: 10px 0;
        z-index: 1000;
    }
}






















