.reuse {
    //   .
    &__text-center {
        text-align: center;
    }
    //    .
    &__text-right {
        text-align: right;
    }
    //  images.
    &__adapt-img {
        display: block;
        max-width: 100%;
        height: auto;
    }
    //  margin-bottom.
    &__mb-0 {
        margin-bottom: 0;
    }
    //  () .
    &__hide {
        display: none;
    }
}
