// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-
// -#- IMPORTS - general styles -#-
// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-
//
// Variables (      ,    ).
@import "../_vars.scss";
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= BLOCK: modal =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
//   .
.modal {
    display: none;
    width: 100%;
    height: 100%;
    padding: 1rem;
    overflow: auto; //   ,    ,   .
    background-color: rgba(#2e2e2f, 0.85);
    position: fixed; //    .
    top: 0;
    left: 0;
    z-index: 1000; //      .
    /*    show,     .   ,  
      ( show     js-). */
    &.show {
        display: flex;
    }
}

//  modal__dialog     .     (  modal).
.modal__dialog {
    width: 100%;
    max-width: 1200px; //    .
    margin: auto; //         .
    background-color: #fff;
    position: relative;
    //
    //      (   90   0    js-).
    transform: rotateX(90deg); //    :   90  (  ).
    transition: transform 0.4s linear; //     .
}

//    .
.modal__close {
    width: 34px;
    height: 34px;
    //     button.
    background: 0;
    border: 0;
    padding: 0;
    cursor: pointer;

    position: absolute; //  modal__dialog.
    top: 0.867rem;
    right: 0.867rem;
    z-index: 5;
    transition: transform 0.2s linear;
    //    .
    &:hover {
        transform: rotate(90deg);
    }
    //   ( )   outline.
    &:focus {
        outline: none;
    }
}

.modal__content {
    padding: 5rem 1rem;
}

.modal__title {
    font-size: 3rem;
    text-align: center;
    color: $violet-dark;
    text-transform: uppercase;
    font-weight: 700;
    line-height: 3.533rem;
    margin-bottom: 3.6rem;
}
.modal__subtitle {
    font-size: 1.067rem;
    text-align: center;
    color: $violet-dark;
    text-transform: uppercase;
    font-weight: 400;
    line-height: 1.8rem;
}
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MODIFIERS: modal (connection at the end of styles .css/.scss) =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
.modal__dialog {
    //   .
    &--small {
        max-width: 900px; //    .
    }
}
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MEDIA QUERIES: modal =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
/*   max-width (      ),
   min-width (      ). */
//
@media ($mobile) {
    .modal__title {
        font-size: 2.3rem;
    }
    .modal__subtitle {
        font-size: 0.82rem;
    }
}

@media ($mobile-sm) {
    .modal__title {
        font-size: 2.1rem;
        text-align: center;
        margin-top: 1.2rem;
    }
    .modal__subtitle {
        text-align: center;
        font-size: 1.2rem;
    }
}

