//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= BLOCK: sandwichMenu =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
/* /        sandwichMenu,
    */
//
.sandwichMenu {
    width: 30px;
    padding: 10px 0;
    display: none; //    sandwichMenu .
    position: absolute; //  body.
    top: 24px;
    right: 24px;
    z-index: 1;
    cursor: pointer;
    //      ( button).
    font-size: 0; //      .
    color: transparent; //     .
    border: 0; //   border  .
    background: none; //   background  .
    &:focus {
        outline: none;
    }
    &__item { //     sandwichMenu.
        display: block;
        width: 100%;
        height: 3px;
        background-color: $violet-dark;
        position: absolute; //  sandwichMenu.
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        transition: background 0.2s linear; //      sandwichMenu    .
        //       sandwichMenu.
        &::before,
        &::after {
            content: "";
            width: 100%;
            height: 3px;
            position: absolute;
            left: 0;
            background-color: $violet-dark;
            z-index: 1;
            transition: transform 0.2s linear; //    sandwichMenu    .
        }
        //     sandwichMenu.
        &::before {
            top: -8px;
        }
        //     sandwichMenu.
        &::after {
            bottom: -8px;
        }
    }
    //    sandwichMenu ( sandwichMenu ).
    &.active {
        .sandwichMenu__item {
            background: none; //     sandwichMenu.
            //     sandwichMenu.
            &::before {
                transform-origin: left top;
                transform: rotate(45deg) translateY(-3px);
            }
            //     sandwichMenu.
            &::after {
                transform-origin: left bottom;
                transform: rotate(-45deg) translateY(3px);
            }
        }
    }
}
