// Variables (      ,    ).
@import "../_vars.scss";
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= BLOCK: social =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
.social {
    display: flex;
    margin-bottom: 42px;
    &__link {
        //       (.. margin-left       ).
        + .social__link {
            margin-left: 24px;
        }
        &-facebook {
            margin-top: 5px;
        }
        &-twitter {
            margin-top: 3px;
        }
        &-instagram {
            margin-top: 5px;
        }
        &-telegram {
            margin-top: 4px;
        }
        &-whatsapp {
            margin-top: 5px;
        }
        transition: transform 0.2s linear;
        //     .
        &:hover {
            transform: translateY(-5px);
        }
    }
}
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MODIFIERS: social (connection at the end of styles .css/.scss) =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//

//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MEDIA QUERIES: social =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
/*   max-width (      ),
   min-width (      ). */
//
@media ($tablet) {
    .social {
        &__link {
            //       (.. margin-left       ).
            + .social__link {
                margin-left: 18px;
            }
        }
    }
}
//
@media ($mobile) {
    .social {
        &__link {
            //       (.. margin-left       ).
            + .social__link {
                margin-left: 17px;
            }
        }
    }
}
//
@media ($mobile-sm) {
    .social {
        &__link {
            //       (.. margin-left       ).
            + .social__link {
                margin-left: 24px;
            }
        }
    }
}
//
@media (max-width: 404px) {
    .social {
        &__link {
            //       (.. margin-left       ).
            + .social__link {
                margin-left: 8px;
            }
        }
    }
}





