// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-
// -#- IMPORTS - general styles -#-
// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-
// Variables (      ,    ).
@import "../_vars.scss";
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= BLOCK: btn =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
/*    ,    
 (button  a).     . */
//  :   ( em)      ( rem).
.btn {
    display: inline-block;
    vertical-align: top;
    padding: 1.05em 1.7em; //  font-size ( rem).
    min-width: 170px;

    border: 1px solid $violet-bold;
    border-radius: 1.8em; //  font-size ( rem).
    background: none;
    cursor: pointer;

    font-family: inherit;
    font-size: 1.067rem;
    line-height: 1.1;
    color: $violet-dark;
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;

    transition: background-color 0.2s linear, color 0.2s linear, box-shadow 0.2s linear, border-color 0.2s linear;

    &:focus {
        outline: none;
    }
    //      .
    &:hover {
        background-color: $violet-dark;
        color: $white;
        box-shadow: 2px 2px 10px rgba($violet-dark, 0.24);
        border-color: $violet-dark;
    }
    //       (.. margin-left       ).
    + .btn {
        margin-left: 1rem;
    }
}
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MODIFIERS: btn (connection at the end of styles .css/.scss) =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
.btn {
    //  .
    &--small {
        font-size: 0.933rem;
        font-weight: 700;
        border-width: 2px;
        padding: 0.7em 1.1em;
        min-width: 0;
    }
    //   .
    &--thin {
        min-width: 145px;
        font-size: 0.8rem;
    }

}
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MEDIA QUERIES: btn =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
/*   max-width (      ),
   min-width (      ). */
//
@media ($tablet) {
    .btn {
        font-size: 1rem;
        padding: 1em 1.5em; //  font-size ( rem).
        min-width: 150px;
        &--thin {
            min-width: 145px;
            font-size: 0.8rem;
        }
    }
}

@media ($mobile-sm) {
    .btn {
        display: block;
        width: 100%;
        //       (.. margin-left       ).
        + .btn {
            margin-left: 0;
            margin-top: 1rem;
        }
    }
}











