//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= BLOCK: contact =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
.contact {
    display: flex;
    &__left {
        width: 47%;
        padding: 2rem;
    }
    &__right {
        width: 53%;
        //background: $violet-dark url("https://place-hold.it/480x700/660099/ffffff&fontsize=24") center center no-repeat;
        position: relative;
        //      contact__right,  .
        overflow: hidden;
        &-photo {
            width: auto;
            height: 100%;
            position: absolute; //  contact__right.
            top: 50%;
            left: 50%;
            z-index: 1;
            //     .
            transform: translate3d(-50%, -50%, 0);
        }
    }
    &__info {
        margin: 2rem 0 3rem;
        font-size: 1.1rem;
        font-weight: 300;
        color: $violet-dark;
        &-item {
            display: flex;
            align-items: center;
            margin-bottom: 0.75rem;
            &:last-child {
                margin-bottom: 0;
            }
        }
        //   flex     .
        &-phone {
            display: flex;
            align-items: center;
        }
        //   flex       .
        &-email {
            display: flex;
            align-items: center;
        }
        &-phone,
        &-email {
            position: relative;
            //   (  ).
            &::after {
                content: "";
                width: 100%;
                height: 1px;
                background-color: $violet-red;
                position: absolute; //  contact__info-phone  contact__info-email .
                bottom: -3px;
                left: 0;
                z-index: 1;
                transition: width 0.2s linear; //  (  width).
            }
            //      .
            &:hover {
                &::after {
                    width: 0;
                }
            }
        }
        &-icon {
            margin-right: 0.75rem;
        }

    }
}

