//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= MEDIA QUERIES: contact =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
/*   max-width (      ),
   min-width (      ). */
//
@media ($mobile) {
    .contact {
        &__left {
            padding: 1rem;
        }
        &__info {
            margin: 1.5rem 0 1rem;
            font-size: 0.933rem;
            &-item {
                margin-bottom: 0.7rem;
            }
        }
    }
}

@media ($mobile-sm) {
    .contact {
        &__left {
            width: 100%;
            padding: 2rem;
        }
        &__info {
            font-size: 1.1rem;
        }
        &__right {
            display: none;
        }
    }
}
//
@media (max-width: 390px) {
    .contact {
        &__info {
            font-size: 0.933rem;
        }
    }
}
//
@media (max-width: 346px) {
    .contact {
        &__info {
            font-size: 0.83rem;
        }
    }
}





















